import React from "react"
import * as S from "./styles"

export default function Button(props) {
  let tagName
  props.href ? (tagName = "a") : (tagName = props.as)

  return (
    <S.Button
      id={props.id}
      className={props.className}
      onClick={props.onClick}
      type={props.type}
      //
      as={tagName}
      size={props.size}
      variant={props.variant}
      transparent={props.transparent}
      disabled={props.disabled}
      block={props.block}
      // a
      href={props.href}
      target={props.target}
      role={props.role}
      // input
      value={props.value}
    >
      {props.children}
    </S.Button>
  )
}
