import { css } from "styled-components"

const xxxLarge = css`
  .xxxLarge-high-title {
    font-size: 40px;
    line-height: 48px;
    font-weight: normal;
  }
  .xxxLarge-h1 {
    font-size: 34px;
    line-height: 41px;
    font-weight: normal;
  }
  .xxxLarge-h3 {
    font-size: 26px;
    line-height: 32px;
    font-weight: normal;
  }
  .xxxLarge-body {
    font-size: 23px;
    line-height: 29px;
    font-weight: normal;
  }
  .xxxLarge-callout {
    font-size: 22px;
    line-height: 28px;
    font-weight: normal;
  }
  .xxxLarge-footnote {
    font-size: 19px;
    line-height: 24px;
    font-weight: normal;
  }
  .xxxLarge-caption {
    font-size: 18px;
    line-height: 23px;
    font-weight: normal;
  }
`

export default xxxLarge
