import React from "react"
import { MDXProvider } from "@mdx-js/react"
import * as S from "./styles"
import { ThemeProvider } from "styled-components"
import { GlobalStyles } from "styles"
import { lightTheme, darkTheme } from "styles/theme"
import Sidebar from "projectOrganisms/Sidebar"
import MainNav from "projectOrganisms/MainNav"
import Button from "globalAtoms/Button"

const shortcodes = { Button }

export default function Layout(props) {
  return (
    <ThemeProvider theme={darkTheme}>
      <GlobalStyles />
      <MDXProvider components={shortcodes}>
        <S.Layout as="main" className={props.className} id={props.id}>
          <Sidebar />
          <MainNav />
          <div className="main-content mt-48 mb-56">{props.children}</div>
        </S.Layout>
      </MDXProvider>
    </ThemeProvider>
  )
}
