import { styleVariables } from "projectSettings"
import { css } from "styled-components"

const code = css`
  pre {
    margin-bottom: 48px;
  }
  code[class*="language-"],
  pre[class*="language-"] {
    font-size: 15px;
    line-height: 20px;
    color: ${({ theme }) => theme.tint_pink};
    white-space: pre;
    word-break: normal;
    word-wrap: normal;
    tab-size: 2;
    hyphens: none;
  }

  @media print {
    pre[class*="language-"],
    code[class*="language-"] {
      text-shadow: none;
    }
  }

  // Code blocks
  pre[class*="language-"] {
    overflow-x: auto;
    border-radius: ${styleVariables.root.primaryBorderRadius};
  }

  :not(pre) > code[class*="language-"],
  pre[class*="language-"] {
    background-color: ${({ theme }) => theme.background_tertiary};
  }

  // Inline code
  :not(pre) > code[class*="language-"] {
    font-size: 17px;
    line-height: 22px;
    //color: ${({ theme }) => theme.tint_teal};
    color: ${({ theme }) => theme.tint_pink};
    background-color: transparent;
    font-family: ${styleVariables.root.fontFamilySansSerif};
  }

  // Tokens
  .token.doctype .token.doctype-tag {
    color: #569cd6;
  }

  .token.doctype .token.name {
    color: #9cdcfe;
  }

  .token.comment,
  .token.prolog {
    color: #6a9955;
  }

  .token.punctuation,
  .language-html .language-css .token.punctuation,
  .language-html .language-javascript .token.punctuation {
    color: #d4d4d4;
  }

  .token.property,
  .token.tag,
  .token.boolean,
  .token.number,
  .token.constant,
  .token.symbol,
  .token.inserted,
  .token.unit {
    color: #b5cea8;
  }

  .token.selector,
  .token.attr-name,
  .token.string,
  .token.char,
  .token.builtin,
  .token.deleted {
    color: #ce9178;
  }

  .language-css .token.string.url {
    text-decoration: underline;
  }

  .token.operator,
  .token.entity {
    //color: ${styleVariables.color.gray};
    color: ${({ theme }) => theme.tint_pink};
  }

  .token.operator.arrow {
    color: #569cd6;
  }

  .token.atrule {
    color: #ce9178;
  }

  .token.atrule .token.rule {
    color: #c586c0;
  }

  .token.atrule .token.url {
    color: #9cdcfe;
  }

  .token.atrule .token.url .token.function {
    color: #dcdcaa;
  }

  .token.atrule .token.url .token.punctuation {
    color: #d4d4d4;
  }

  .token.keyword {
    color: #569cd6;
  }

  .token.keyword.module,
  .token.keyword.control-flow {
    color: #c586c0;
  }

  .token.function,
  .token.function .token.maybe-class-name {
    color: #dcdcaa;
  }

  .token.regex {
    color: #d16969;
  }

  .token.important {
    color: #569cd6;
  }

  .token.italic {
    font-style: italic;
  }

  .token.constant {
    color: #9cdcfe;
  }

  .token.class-name,
  .token.maybe-class-name {
    color: ${styleVariables.color.gray};
  }

  .token.console {
    color: #9cdcfe;
  }

  .token.parameter {
    color: #9cdcfe;
  }

  .token.interpolation {
    color: #9cdcfe;
  }

  .token.punctuation.interpolation-punctuation {
    color: #569cd6;
  }

  .token.boolean {
    color: #569cd6;
  }

  .token.property,
  .token.variable,
  .token.imports .token.maybe-class-name,
  .token.exports .token.maybe-class-name {
    color: #9cdcfe;
  }

  .token.selector {
    color: #d7ba7d;
  }

  .token.escape {
    color: #d7ba7d;
  }

  .token.tag {
    color: #569cd6;
  }

  .token.tag .token.punctuation {
    color: ${styleVariables.color.gray};
  }

  .token.cdata {
    color: #808080;
  }

  .token.attr-name {
    color: ${({ theme }) => theme.tint_pink};
  }

  .token.attr-value,
  .token.attr-value .token.punctuation {
    color: ${({ theme }) => theme.tint_pink};
  }

  .token.attr-value .token.punctuation.attr-equals {
    color: ${styleVariables.color.gray};
  }

  .token.entity {
    color: #569cd6;
  }

  .token.namespace {
    color: #4ec9b0;
  }

  // Language Specific

  pre[class*="language-javascript"],
  code[class*="language-javascript"],
  pre[class*="language-jsx"],
  code[class*="language-jsx"],
  pre[class*="language-typescript"],
  code[class*="language-typescript"],
  pre[class*="language-tsx"],
  code[class*="language-tsx"] {
    //color: ${({ theme }) => theme.text_secondary};
    color: ${styleVariables.color.gray};
  }

  pre[class*="language-css"],
  code[class*="language-css"] {
    color: #ce9178;
  }

  pre[class*="language-html"],
  code[class*="language-html"] {
    color: #d4d4d4;
  }

  .language-regex .token.anchor {
    color: #dcdcaa;
  }

  .language-html .token.punctuation {
    color: #808080;
  }

  // line numbers

  pre[class*="language-"].line-numbers {
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: max-content 1fr;
    counter-reset: linenumber;
  }

  pre[class*="language-"].line-numbers > code {
    padding: 16px !important;
    white-space: inherit;
  }

  .line-numbers .line-numbers-rows {
    pointer-events: none;
    user-select: none;
  }
  .line-numbers-rows {
    display: block;
    grid-column: 1;
    padding: 16px !important;
    box-shadow: 1px 0px 0px ${({ theme }) => theme.separator_non_opaque};
  }

  .line-numbers-rows > span {
    display: block;
    counter-increment: linenumber;
  }

  .line-numbers-rows > span:before {
    content: counter(linenumber);
    color: ${({ theme }) => theme.gray_01};
    display: block;
  }
`
export default code
